body {
    background-color: #444;
    color: #fff;
    text-align: center;
    font-size: 20px;
    font-family: 'Raleway', sans-serif;
    padding-top: 5%;
}

.logo {
    width: 150px;
    height: auto;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.WalletInfo {
    width: 90%;
    max-width: 700px;
    word-wrap: break-word;
}

.Block {
    border: 1px solid #fff;
    padding: 30px 10px;
    margin: 50px;
}

.Transaction {
    padding: 25px;
}

a, a:hover {
    color: #e66;
    text-decoration: underline;
}

.ConductTransaction, .TransactionPool {
    margin: auto;
    width: 90%;
    max-width: 600px;
}

button {
    margin: 5px;
}

.KnownAddress {
    max-width: 100%;
    word-wrap: break-word;
    margin: auto;
}